import { Component, OnInit } from '@angular/core';
import {SubmitEventService} from '../submit-event.service';
@Component({
  selector: 'app-latest-news',
  templateUrl: './latest-news.component.html',
  styleUrls: ['./latest-news.component.css']
})
export class LatestNewsComponent implements OnInit {

  constructor(private uploadservice: SubmitEventService) { }
  latest_news = [];
  ngOnInit(): void {
    var dNow = new Date();
    var localdate= (dNow.getFullYear()+ '/' + dNow.getDate()+ '/' + dNow.getMonth()+1);
    
    this.uploadservice.getLatestNews(localdate).subscribe((data: any[])=>{
      console.log(data);
      for (var key in data) {
        var value = data[key];
        console.log(key, value)
        this.latest_news =value
      }
      
    })  
  }

}
