import { Component, OnInit } from '@angular/core';
import {SubmitEventService} from '../../submit-event.service'
import {ViewChild, ElementRef, Renderer2} from '@angular/core';

@Component({
  selector: 'app-showfilr',
  templateUrl: './showfilr.component.html',
  styleUrls: ['./showfilr.component.css']
})
export class ShowfilrComponent implements OnInit {
  selectedSRC :any;
  @ViewChild('text', { static: true }) text: any;
  constructor(private uploadservice: SubmitEventService,private renderer : Renderer2) { }
  
  ngOnInit(): void {
    let element: HTMLElement = document.getElementById("modal") as HTMLElement;
    element.click();
  }
  getText(){
    this.selectedSRC = this.uploadservice.returnFile();
    this.renderer.setAttribute(this.text.nativeElement, "src", "data:"+this.selectedSRC)
  }
}
