import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { VisitersListComponent } from '../app/visiters-list/visiters-list.component'
import { BodyComponent } from '../app/body/body.component';
import { LatestNewsComponent } from './latest-news/latest-news.component';
const routes: Routes = [
  {path: "", component:BodyComponent},
  {path: "visitorsList", component:VisitersListComponent},
  {path: "latest_news", component:LatestNewsComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
