import { Injectable } from '@angular/core';
import {HttpClient,HttpEvent,HttpErrorResponse, HttpEventType} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SubmitEventService {
  constructor(private httpClient:HttpClient) { }
  data = [];
  file = "";
  url = "https://newsapi.org/v2/everything?q=bitcoin&from=2020-03-25&sortBy=publishedAt&apiKey=1848b5465b1449d78d10c2991b1bea98"
  public sendFormData(post){
    this.data.push(JSON.stringify(post))
    localStorage.setItem("formdata",JSON.stringify(this.data));
  }
  public getData(){
    localStorage.getItem("formdata");
  }

  public getLatestNews(date){
    return this.httpClient.get("https://newsapi.org/v2/everything?q=bitcoin&from='"+date+"'&sortBy=publishedAt&apiKey=1848b5465b1449d78d10c2991b1bea98")
  }

  public sendFile(f){
    this.file = f;
  }
  public returnFile(){
    return this.file;
  }
}
